.u-video {
    display: inline-block;
    position:relative;
    overflow: hidden;
    background-color:#ccc;
    &-play {
        position: absolute;
        top:50%;
        left:50%;
        -webkit-transform: translate(-50%,-50%);
        transform:translate(-50%,-50%);
        width:15%;
        z-index:99;
    }
    &-controls {
        display: flex;
        display: -webkit-flex;
        position:absolute;
        bottom:0;
        width:100%;
        height:2rem;
        line-height:2rem;
        font-size:.8rem;
        color:#fff;
        background-color:#000;
        opacity:.6;
        -webkit-opacity:.6;
    }
    &-bar {
        flex:6;
        -webkit-flex:6;
    }
    &-progressBar {
        position:relative;
        margin:.75rem 5%;
        width:90%;
        height:.5rem;
        background-color:#666;
        opacity:.6;
        border-radius:10px;
    }
    &-timeBar {
        position:absolute;
        top:0;
        left:0;
        width:20%;
        height:100%;
        background-color:#fff;
        opacity:.8;
        border-radius:10px;
    }
    &-time {
        flex:3;
        -webkit-flex:3;
        text-align: center;
    }
    &-ful {
        flex:2;
        -webkit-flex:2;
        text-align:center;
    }
}